
.component-container{
    /* height:auto; */
    width:fit-content;    
}

.component{
    list-style-type: none;
    padding-left:0px;
    display: flex;
    justify-content: center;
    padding-bottom: 0px;
    align-items: center;
    flex-direction: row;
    margin-bottom: 0px;
}

.label{
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    font-family: "Source Sans Pro", sans-serif;
    font-weight: 500;
    font-size: 1rem;
    cursor: pointer;
    color:black;
    padding: 10px;
    white-space: nowrap;
    width:100%; 
    border-bottom: solid 2px transparent;
    transition: 0.1s ease-in-out all;
     
}

.label:hover{
    border-bottom: solid 2px black;  
}

.label > i {
    margin-left: 10px;
}
